/* http://meyerweb.com/eric/tools/css/reset/ 
    v2.0 | 20110126
    License: none (public domain)
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* extra resetting  */
/* makes all images responsive */
img {
  max-width: 100%;
  height: auto; }

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/* ------------------------------------------------ */
.nav {
  z-index: 1;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 20px 0 20px;
  height: 90px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) 1%, rgba(0, 0, 0, 0.5) 61%, transparent); }
  .nav .register {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50px;
    right: 13px;
    align-items: center; }
  .nav .menu-btn .btn-line {
    width: 28px;
    height: 1px;
    margin: 0 0 5px 0;
    background: #fff; }
  .nav .menu-btn.close .btn-line:nth-child(1) {
    transform: translateY(6px);
    transition: transform 1s; }
  .nav .menu-btn.close .btn-line:nth-child(2) {
    transition: transform 1s;
    opacity: 0; }
  .nav .menu-btn.close .btn-line:nth-child(3) {
    transform: translateY(-6px);
    transition: transform 1s; }

.banner {
  height: 100vh;
  background-image: url("/images/banner1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative; }
  .banner .menu-main {
    position: absolute;
    top: 100px;
    visibility: hidden; }
    .banner .menu-main.show {
      visibility: visible; }
